import { animate, state, style, transition, trigger } from '@angular/animations';
import { Component, ElementRef, OnInit, ViewChild } from '@angular/core';
import { MessageService } from 'primeng/api';
import { BehaviorSubject, combineLatest, Observable } from "rxjs";

@Component({
  providers: [MessageService],
  selector: 'app-calculator',
  templateUrl: './calculator.component.html',
  styleUrls: ['./calculator.component.scss'],
  animations: [
    trigger("calc", [
      state("left", style({
        left: "-100%",
        opacity: 0
      })),
      state("right", style({
        left: "100%",
        opacity: 0,
      })),
      state("center", style({
        left: 0,
        opacity: 1,
      })),
      transition("* => *", [
        animate('0.5s ease-in-out')
      ])

    ]),
    trigger("btns", [
      state("shown", style({
        opacity: 1
      })),
      state("hidden", style({
        opacity: 0
      })),
      transition("* => *", [
        animate("0.3s ease-in")
      ])
    ])
  ]
})
export class CalculatorComponent implements OnInit {

  @ViewChild("cnt") rect : ElementRef<HTMLDivElement>;


  MAX_PAGES = 6;
  btnsState = "hidden";

  bedroomsSubject$ = new BehaviorSubject(1);
  bedrooms$ = this.bedroomsSubject$.asObservable();

  set bedrooms(value: number) {
    this.bedroomsSubject$.next(value);
  }
  get bedrooms() {
    return this.bedroomsSubject$.value;
  }

  bathroomsSubject$ = new BehaviorSubject(0);
  bathrooms$ = this.bathroomsSubject$.asObservable();
  set bathrooms(value: number){
    this.bathroomsSubject$.next(value);
  }
  get bathrooms() {
    return this.bathroomsSubject$.value;
  }

  oftenSubject$ = new BehaviorSubject(1);
  often$ = this.oftenSubject$.asObservable();
  set often(value: number){
    this.oftenSubject$.next(value);
  }
  get often() {
    return this.oftenSubject$.value;
  }


  selectedExtrasSubject$ = new BehaviorSubject([]);
  selectedExtras$ = this.selectedExtrasSubject$.asObservable();
  set selectedExtras(value: any[]) {
    this.selectedExtrasSubject$.next(value);
  }
  get selectedExtras() {
    return this.selectedExtrasSubject$.value;
  }

  typeOfServiceSubject$ = new BehaviorSubject("regular");
  typeOfService$ = this.typeOfServiceSubject$.asObservable();
  set typeOfService(value: string) {
    this.typeOfServiceSubject$.next(value);
  }
  get typeOfService(){
    return this.typeOfServiceSubject$.value;
  }



  price: number = 0;
  page = 1;

  sub1 = "center";
  sub2 = "right";

  getAnimation = (value) => {
    if (value > this.page) {
      return "right";
    } else if (value < this.page) {
      return "left";
    } else {
      return "center";
    }
  }

  next = () => {
    if (this.page < this.MAX_PAGES) {
      this.page++;
    }
  }

  back = () => {
    if (this.page > 1) {
      this.page--;
    }
  }

  calculatePrice = (value) => {
    console.log(value);
    let basePrice = 0;
    let extraBedroomPrice = 0;
    let extraBathroomPrice = 0;

    switch(this.typeOfService) {
      case "regular":
        basePrice = 90;
        extraBedroomPrice = 15;
        extraBathroomPrice = 15;
        break;
      case "spring":
        basePrice = 250;
        extraBedroomPrice = 22;
        extraBathroomPrice = 22;
        break;
      default:
        basePrice = 320;
        extraBedroomPrice = 25;
        extraBathroomPrice = 25;
    }

    const extraBedrooms = this.bedrooms - 1;
    const extraBathrooms = (this.bathrooms >= 1) ? this.bathrooms - 1 : 0;

    let extras = 0;
    for (let eachExtra of this.selectedExtras) {
      switch(eachExtra) {
        case "oven":
          extras += 50;
          break;
        case "microwave":
          extras += 15;
          break;
        case "window":
          extras += 20;
          break;
        case "bin":
          extras += 15;
          break;
      }
    }


    const multiplier = (this.often > 1) ? (this.often * 0.92) : 1;

    const price = basePrice + (extraBathroomPrice * extraBathrooms) + (extraBedroomPrice * extraBedrooms) + extras;
    const finalPrice = price * multiplier;

    this.price = Math.trunc(finalPrice);
  }

  bookService = () => {
    this.messageService.add({severity: "success", summary: "Success", detail: "Service booked successfully!"})
  }

  constructor(private messageService : MessageService) { }

  mainSubs() : Observable<any> {
   return combineLatest([
    this.typeOfService$,
    this.bedrooms$,
    this.bathrooms$,
    this.selectedExtras$,
    this.often$
   ]);
  }

  ngOnInit(): void {
    this.mainSubs().subscribe(this.calculatePrice);
  }

  ngAfterViewInit() {
    document.addEventListener("scroll", () => {
      
      const rectClient = this.rect.nativeElement.getBoundingClientRect();

      if (rectClient.top >= 0 && rectClient.left >= 0 && rectClient.bottom <= (window.innerHeight || document.documentElement.clientHeight) && rectClient.right <= (window.innerWidth || document.documentElement.clientWidth)) {
        this.btnsState = "shown";
      } else {
        this.btnsState = "hidden";
      }


    })
  }

}
